import request from '@/utils/request'
export function activeTrade(id) {
  return request({
    url: `/fd/contract/${id}/active`,
    method: 'put'
  })
}
export function getOrderOpts() {
  return request({
    url: 'order/basic/options',
    method: 'get'
  })
}
export function getOrderNames() {
  return request({
    url: `/order/basic/order_name/options`,
    method: 'get'
  })
}
export function getfdStatus() {
  return request({
    url: `/order/basic/fd_status/options`,
    method: 'get'
  })
}
export function getOrderStatus() {
  return request({
    url: '/order/basic/order_status/options',
    method: 'get'
  })
}

export function getOrderType() {
  return request({
    url: '/order/basic/order_type/options',
    method: 'get'
  })
}
// /weapp_scene/options
export function getOrderScene() {
  return request({
    url: '/order/basic/weapp_scene/options',
    method: 'get'
  })
}
export function orderExport(data) {
  return request({
    url: '/order/data_export',
    method: 'get',
    data
  })
}
export function checkExport(data) {
  return request({
    url: '/order/check_export',
    method: 'post',
    data
  })
}
export function getOrderDetail(id) {
  return request({
    url: `/order/${id}/detail`,
    method: 'get'
  })
}

export function closeTrade(id, data) {
  return request({
    url: `/order/${id}/close`,
    method: 'put',
    data
  })
}

export function getOrderProcess() {
  return request({
    url: '/order/process_numbers',
    method: 'get'
  })
}

export function getOrderList(params) {
  return request({
    url: '/order/list',
    method: 'post',
    data: params
  })
}

export function getOrderListParams() {
  return request({
    url: '/order/filter',
    method: 'get'
  })
}

export function applyRefund(id, data) {
  return request({
    url: `/order/${id}/refund`,
    method: 'put',
    data
  })
}

// 获取订单备注

export function getRemark(params) {
  return request({
    url: `/order/log`,
    method: 'get',
    params
  })
}
// 添加订单备注

export function addRemark(data) {
  return request({
    url: `/order/log`,
    method: 'post',
    data
  })
}
