<template>
  <div class="customer__basic">
    <el-row :gutter="24" class="customer__basic-info">
      <el-col :span="24">
        <el-row :gutter="24" class="info-top">
          <el-col :span="3" class="user-avatar">
            <el-avatar :src="userInfo.avatar" :key="userInfo.id"></el-avatar>
          </el-col>
          <el-col :span="7">
            <span class="span-key">客户昵称：</span>
            <span class="span-val" :title="userInfo.nickname">
              {{ formatSpanVal(userInfo.nickname) }}
            </span>
          </el-col>
          <el-col :span="7">
            <div class="item">
              <span class="span-key">微信号：</span>
              <span class="span-val" :title="userInfo.weapp_id">
                {{ formatSpanVal(userInfo.weapp_id) }}
              </span>
            </div>
          </el-col>
          <el-col :span="7">
            <span class="span-key">用户ID：</span>
            <span class="span-val" :title="userInfo.user_id">
              {{ formatSpanVal(userInfo.user_id) }}
            </span>
          </el-col>
        </el-row>
        <el-row class="align-left" :gutter="24">
          <el-col
            :offset="index % 3 === 0 ? 3 : 0"
            :span="7"
            v-for="(itemBaby, index) in userInfo.babys"
            :key="itemBaby._id"
            class="margin-bottom-10"
          >
            宝宝：
            <span :title="getBabyInfo(itemBaby)">{{
              formatSpanVal(getBabyInfo(itemBaby))
            }}</span>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getCustomerInfo } from '@/api/crmManage.js'
import { ellipsis } from '@/filters'
export default {
  name: 'UserInfo',
  props: {
    customerId: {
      type: String,
      default: ''
    },
    ellipsisLength: {
      type: Number,
      default: 20
    }
  },
  data() {
    return {
      userInfo: {}
    }
  },
  methods: {
    formatSpanVal(spanVal) {
      if (spanVal) {
        return ellipsis(spanVal, this.ellipsisLength)
      }
      return '-'
    },
    getBabyInfo(babyObj) {
      return babyObj.name + '-' + babyObj.info
    },
    getCardUserInfo(customerId) {
      getCustomerInfo(customerId).then(res => {
        if (res.code === 20000) {
          this.userInfo = res.data
        }
      })
    }
  },
  watch: {
    customerId(val) {
      this.getCardUserInfo(val)
    }
  },
  created() {
    this.getCardUserInfo(this.customerId)
  }
}
</script>

<style scoped lang="scss">
.customer__basic {
  .customer__basic-info {
    margin: 8px 0 0 !important;
    padding: 5px 0;
    border-radius: 2px;
    .user-avatar {
      vertical-align: middle;
      margin-top: -20px;
      padding-top: 10px;
    }
    .info-top {
      padding-top: 16px;
    }
    .el-col {
      line-height: 14px;
      font-size: 14px;
      font-family: PingFangSC-Medium;
      color: #747e8f;
      letter-spacing: 0;
      span {
        color: rgb(148, 153, 163);
      }
    }
  }
}
</style>
