<template>
  <el-dialog
    title="跟进客户"
    :visible.sync="followDialogVisible"
    width="45%"
    @opened="resetFollowForm"
    :before-close="handleClose"
    :center="false"
  >
    <div class="margin-bottom-30">
      <customer-basic-info
        :customer-id="customerId"
        :ellipsis-length="12"
      ></customer-basic-info>
    </div>
    <div class="follow">
      <el-form
        :model="followForm"
        :label-position="'right'"
        ref="followForm"
        label-width="120px"
      >
        <oprator-action-select
          v-model="followForm.action"
          :is-open="true"
          prop="action"
          :rules="[
            { required: true, message: '请选择运营动作', trigger: 'change' }
          ]"
        ></oprator-action-select>
        <el-form-item
          label="运营动作日期"
          prop="actionDate"
          :rules="[
            { required: true, message: '请选择运营动作日期', trigger: 'blur' }
          ]"
        >
          <div class="follow-dialog__picker">
            <el-date-picker
              :picker-options="pickerOptions"
              class="follow-dialog__date-picker"
              v-model="followForm.actionDate"
              type="date"
              placeholder="请选择运营动作日期"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item label="备注" prop="desc">
          <el-input
            type="textarea"
            :rows="3"
            show-word-limit
            placeholder="请输入内容"
            v-model="followForm.desc"
          >
          </el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="form-tip">
      运营动作日期仅能选择当天及前日，请及时录入跟进信息
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="addFollow">确认提交</el-button>
    </span>
  </el-dialog>
</template>

<script>
import OpratorActionSelect from '@/components/crm/SelectComponents/OpratorActionSelect'
import CustomerBasicInfo from './CustomerBasicInfo'
import { pickerOptionsTodayAndBefore } from '@/utils/crm'
import { followCustomer } from '@/api/crmManage'
export default {
  name: 'followCustomerDialog',
  props: {
    followDialogVisible: {
      type: Boolean,
      default: false
    },
    customerId: {
      type: String,
      default: ''
    }
  },
  components: {
    OpratorActionSelect,
    CustomerBasicInfo
  },
  data() {
    return {
      pickerOptions: pickerOptionsTodayAndBefore,
      followForm: {
        action: '',
        actionDate: '',
        desc: ''
      }
    }
  },
  methods: {
    addFollow() {
      // 表单验证成功，抛出关闭事件
      this.$refs.followForm.validate(valid => {
        if (valid) {
          const params = {
            opration_action: this.followForm.action, //客户动作
            desc: this.followForm.desc,
            action_at: this.followForm.actionDate
          }
          followCustomer(this.customerId, params).then(res => {
            if (res.code === 20000) {
              this.$message.success('跟进用户成功')
              this.$emit('followSuccess')
            }
          })
        } else {
          return false
        }
      })
    },
    handleClose() {
      this.$emit('closeDialog')
    },
    resetFollowForm() {
      this.$refs.followForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
.follow {
  padding: 0 30px;
  line-height: 30px;
  font-size: 14px;
}
.form-tip {
  margin-right: 30px;
  font-size: 12px;
  text-align: right;
  color: #f8747f;
}
</style>
<style lang="scss">
.follow {
  .el-select {
    width: 100%;
  }
}
</style>
