export const pickerOptionsTodayAndBefore = {
  disabledDate(time) {
    return (
      time.getTime() < Date.now() - 1000 * 60 * 60 * 24 * 2 ||
      time.getTime() > Date.now()
    )
  }
}
export const pickerOptionsBeforeToday = {
  disabledDate(time) {
    return time.getTime() > Date.now()
  }
}

export function switchDotStyle(timePot) {
  switch (timePot.action_type) {
    case 'add':
      return {
        dotColor: '#409EFF',
        iconType: 'iconico_entry'
      }
    case 'follow':
      return {
        dotColor: '#FFB718',
        iconType: 'iconico_operationx'
      }
    case 'relative':
      return {
        dotColor: '#67C23A',
        iconType: 'iconico_relevancex'
      }
    case 'out_service':
      return {
        dotColor: '#F56C6C',
        iconType: 'iconico_relievex'
      }
    case 'add_order':
      return {
        dotColor: '#409EFF',
        iconType: 'iconico_create_orderx'
      }
    default:
      return {}
  }
}
